Store.AttributeDefinitionsPageController = function () {
  var currentAttributes = []; // a JSON array of attributes loaded from the server
  var savedAttributes = [];
  var attributeIdentifiersToDelete = [];
  var changed = false;
  var attributeCounter = 0;

  /**
   * Gets the store's attribute defintions from the server.
   *
   * @return {Async}
   */
  this.loadAttributeDefinitions = function () {
    var that = this;
    return Base.load(
      Base.req('GET', 'attributes/definitions'), 'Getting definitions').then(
      function (value) {
        currentAttributes = Util.readJSON(value) || {};
        _.each(currentAttributes,
          function (it) {
            it.disabled = true;
            // AG's JSON writer emits nil as [] so we need to coerce it to false.
            if (typeof(it.ordered) === 'object') {
              it.ordered = false;
            }
            it.identifier = that.nextAttributeIndex();
          });
        savedAttributes = _.each(currentAttributes, _.identity);
      });
  };

  this.nextAttributeIndex = function () {
    return attributeCounter++;
  };

  this.getDefinitions = function () {
    return currentAttributes;
  };

  /**
   * Delete the attribute whose identifier matches.
   *
   * @param {string} identifier - Attribute identifier.
   */
  this.deleteDefinition = function (identifier) {
    attributeIdentifiersToDelete.push(identifier);
    currentAttributes = _.filter(currentAttributes, function (definition) {
      return (definition.identifier !== identifier);
    });
    this.updatePage(true);
  };

  /**
   * Try to save any changes to the server
   */
  this.save = function () {
    Layout.removeMessages();
    this.removeDeletedAttributes();
    this.sendNewAttributeDefinitions();
    _.each(currentAttributes, function (attribute) {
      attribute.disabled = true;
    });
    savedAttributes = currentAttributes;
    attributeIdentifiersToDelete = [];
  };

  /**
   * Revert back to the state of the last save
   */
  this.revert = function () {
    Layout.removeMessages();
    currentAttributes = savedAttributes;
    attributeIdentifiersToDelete = [];
    this.updatePage(false);
  };

  /**
   * Add an empty definition to the list and re-display the definitions
   */
  this.addEmptyDefinition = function () {
    this.updateDefinitionsFromPage();
    currentAttributes.push({
      'name': '',
      'minimum-number': null,
      'maximum-number': null,
      'allowed-values': '',
      'ordered': false,
      'disabled': false,
      'identifier': this.nextAttributeIndex(),
    });
    this.updatePage(true);
  };

  function layoutPageWithAttributes() {
    attributeDefinitionsTemplate.cast($$('attributeDefinitions'), this);
    this.updateButtons();
  }
  this.layoutPageWithAttributes = layoutPageWithAttributes;

  this.removeDeletedAttributes = function () {
    var that = this;
    _.each(attributeIdentifiersToDelete, function (identifier) {
      var attribute = _.find(savedAttributes, function (attribute) {
        return (attribute.identifier === identifier);
      });
      if (attribute) {
        that.sendDeleteRequest(attribute.name).then(undefined, function (message) {
          message = message || 'Cannot delete attribute "' + attribute.name +
                                                          '" Unknown error occurred.';
          Layout.showMessage(message, true, true);
        });
      }
    });
  };

  function splitAttributeAllowedValues(input) {
    var values = [];
    var SEARCHING = 0;
    var ESCAPING = 1;
    var QUOTING = 2;
    var isInQuotes = false;
    var token = '';
    var state = SEARCHING;
    var spaces = '';

    for (var i = 0; i < input.length; i++) {
      var char = input.charAt(i);
      switch (state) {
        case SEARCHING:
          if (char === '\\') {
            state = ESCAPING;
          } else if (char === '"') {
            isInQuotes = true;
            state = QUOTING;
          } else if (char === ',') {
            collectCurrentToken();
          } else if (char === ' ') {
            spaces += ' ';
          } else {
            if (token === '') {
              // ignore leading spaces
              spaces = '';
            }
            if (token !== '') {
              token += spaces;
              spaces = '';
            }
            token += char;
          }
          break;
        case QUOTING:
          if (char === '\\') {
            state = ESCAPING;
          } else if (char === '"') {
            isInQuotes = false;
            state = SEARCHING;
          } else {
            token += char;
          }
          break;
        case ESCAPING:
          token = token + char;
          state = isInQuotes ? QUOTING : SEARCHING;
          break;
      }
    }
    collectCurrentToken();

    if (state !== SEARCHING) {
      Base.failMessage('Attribute Definition',
        'Unable to parse Allowed Values in "' + input + '"');
    }

    function collectCurrentToken() {
      if (token !== '') {
        values.push(token);
      }
      token = '';
    }

    return values;
  }

  this.updateDefinitionsFromPage = function () {
    var identifiers = $('.attributeDefinition #identifier');
    var names = $('.attributeDefinition #attributeName');
    var mins = $('.attributeDefinition #attributeMin');
    var maxes = $('.attributeDefinition #attributeMax');
    var alloweds = $('.attributeDefinition #attributeAllowed');
    var ordereds = $('.attributeDefinition #attributeOrdered');

    var newAttributes = [];
    var tmpAttribute;
    _.each(currentAttributes,
      function (definition, index) {
        if (definition.disabled) {
          newAttributes.push(definition);
        } else {
          tmpAttribute = {
            'name': names[index].value,
            'allowed-values':
              splitAttributeAllowedValues(alloweds[index].value),
            'ordered': ordereds[index].checked,
            'disabled': false,
            'identifier': identifiers[index].value,
          };
          if (mins[index].value !== '') {
            tmpAttribute['minimum-number'] = mins[index].value;
          }
          if (maxes[index].value !== '') {
            tmpAttribute['maximum-number'] = maxes[index].value;
          }
          newAttributes.push(tmpAttribute);
        }
      });
    currentAttributes = newAttributes;
  };

  this.sendNewAttributeDefinitions = function () {
    this.updateDefinitionsFromPage();
    var that = this;
    // ?? FIXME, need to behave differently if we get an error from the server.
    // in particular, don't make the new definitions read only.
    Base.load(
      Base.jsonReq(
        'POST',
        'attributes/multiDefinitions',
        {
          'body': Util.writeJSON(_.filter(currentAttributes,
            function (definition) {
              return (definition.name.length > 0) && (!definition.disabled);
            })),
          'contentType': 'application/json',
        }),
      'Sending definitions', function () {
        that.updatePage(false);
      });
  };

  this.sendDeleteRequest = function (name) {
    return Base.jsonReq('DELETE',
     'attributes/definitions?name=' + name + '&error=false');
  };

  this.updatePage = function (hasChanged) {
    changed = hasChanged;
    this.layoutPageWithAttributes();
    this.updateButtons();
  };

  this.updateButtons = function () {
    $('#buttonSave').prop('disabled', !changed);
    $('#buttonRevert').prop('disabled', !changed);
  };
};

